import * as React from 'react';
import Hr from './components/hr';
import { MAPPING, parseJsonMLToValue } from '@ali/4ever-utils';
import isSelectionInHr from './utils/isSelectionInHr';

const renderNode = {
  hr: (props, controller) => {
    const { node, isSelected } = props;
      const payload = node.data || {};
      const parsedPayload = parseJsonMLToValue(payload, MAPPING);
      return (
        <Hr
          {...props}
          isSelected={isSelected && isSelectionInHr(controller)}
          payload={parsedPayload}
        />
      );
  },
}

export default renderNode;

